.calculator {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: auto;
  width: 100%;
  height: 100%;
}

.input {
  grid-column-start: 1;
  grid-column-end: 5;
  background-color: grey;
  color: white;
  text-align: right;
}

.zerobtn {
  grid-column-start: 1;
  grid-column-end: 3;
}

.sidebtn {
  background-color: orange;
}
